@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.main--nosotros .main__contenedor {
  margin-top: 5rem;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .main--nosotros .main__contenedor {
    flex-direction: row;
    gap: 2rem;
  }
}
.main--nosotros .main__contenedor .main__imagen {
  flex: 1;
}
.main--nosotros .main__contenedor .main__contenido {
  flex: 1;
}
.main--nosotros .main__contenedor .texto-chico:after {
  content: "";
  display: block;
  height: 0.3rem;
  background-color: #e1e1e1;
  margin-top: 1rem;
}
.main--nosotros .main__contenedor h2 {
  text-align: start;
}
.main--nosotros .main__contenedor .resaltar-texto {
  font-size: 3rem;
}
.main--nosotros .main__listas {
  display: flex;
  gap: 2rem;
}
.main--nosotros .main__listas ul {
  list-style: none;
}
.main--nosotros .main__listas ul li {
  margin-bottom: 2rem;
}

.alerta {
  color: #FFFFFF;
  padding: 0.5rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 2rem 0;
}
.alerta.error {
  background-color: rgb(230, 17, 17);
}
.alerta.exito {
  background-color: rgb(5, 255, 26);
}

.imagen-small {
  width: 20rem;
  margin-bottom: 2rem;
}

.medicos {
  margin: 5rem 0;
}
@media (min-width: 480px) {
  .medicos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }
}
@media (min-width: 768px) {
  .medicos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.medicos__elemento {
  position: relative;
  margin-bottom: 2rem;
}
@media (min-width: 480px) {
  .medicos__elemento {
    margin-bottom: 0;
  }
}
.medicos__elemento form, .medicos__elemento a {
  position: absolute;
}
.medicos__elemento a {
  top: -1rem;
  right: -2rem;
}
.medicos__elemento form {
  bottom: 7rem;
  left: -2rem;
}
.medicos__elemento .medicos__contenido {
  background-color: #262626;
  padding: 2rem;
}
.medicos__elemento .medicos__contenido p {
  margin: 0;
  color: #FFFFFF;
  text-align: center;
  font-size: 1.5rem;
}
.medicos__elemento .medicos__contenido p span {
  font-weight: 700;
}